<template>
    <div>
        <Slide right>
                <span @click="menuju('/')"> Home
                    <font-awesome-icon icon="caret-left" v-if="currentPage == '/'" class="hambaga-active"/></span>

                <span @click="menuju('/about')"> About 
                    <font-awesome-icon icon="caret-left" v-if="currentPage == '/about'" class="hambaga-active"/></span>
            
                <span @click="menuju('/works')"> Works 
                    <font-awesome-icon icon="caret-left" v-if="currentPage == '/works'" class="hambaga-active"/></span>

                <span @click="menuju('/contact')"> Contact
                    <font-awesome-icon icon="caret-left" v-if="currentPage == '/contact'" class="hambaga-active"/></span>
            
                <span @click="menuju('/inspect')"> Inspect
                    <font-awesome-icon icon="caret-left" v-if="currentPage == '/inspect'" class="hambaga-active"/></span>
            
        </Slide>
    </div>
</template>

<script>
    import { Slide } from 'vue-burger-menu'
    import {mapGetters} from 'vuex'

    export default {
        name:'HamburgerMenu',
        components: {
            Slide
        },
        computed: {
            currentPage() {
                return this.$route.path;
            },
            ...mapGetters(['getLinkColor'])
        },
        methods: {
            menuju(path) {
                if (this.$route.path !== path) this.$router.push(path);
                else this.$toast.error("You already here!", {timeout: 1500, position: "bottom-right"});
            }
        }

    }
</script>

<style>
.hambaga-active{
    color: #FEC257;
    margin-top: 5px;
    margin-left: 7px;
}

</style>