<template>
  <div v-cloak id="app" 
    :style="{backgroundColor: getBackgroundColor}">
      <Navbar v-if="!getIsPhone && this.$route.path != '/under_construction' && this.$route.path != '/error'" />
      <HamburgerMenu v-else-if="this.$route.path != '/under_construction' && this.$route.path != '/error'"/>
      <transition 
        name="router-transition"
        leave-active-class="animated router-animate"
      >
        <router-view />
      </transition> 
    <div :hidden="!warning">
      <div style="width:3840px;height:3840px;background:rgba(0,0,0,0.999);
      position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);"></div>
      <div style=" border-radius:10px;width:200px;height:200px;background-color:#F4F6F9; z-index:99999;position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);">
        <h5>Dear visitors</h5>
        <p style="text-align:center">
          This site do not meant to be loaded on mobile using landscape mode. I'm not spend so much time working on responsiveness sooooooo ... not sorry ðŸ˜œ
        </p>
      </div>

    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import Navbar from '@/components/Navbar.vue';
import HamburgerMenu from '@/components/HamburgerMenu.vue';

export default {
  name: 'app',
  created() {
    this.triggerWarning();
    window.addEventListener("resize", this.triggerWarning);
  },
  destroyed() {
    window.removeEventListener("resize", this.triggerWarning);
  },
  data() {
    return {
      warning: false
    }
  },
  computed: {
    ...mapGetters(['getBackgroundColor','getIsPhone','getIsLandscape']),
  },
  components: {
    Navbar,
    HamburgerMenu
  },
  methods: {
    ...mapActions(['checkPhone','checkLandscape']),
    triggerWarning(){
      this.$store.dispatch('checkPhone');
      this.$store.dispatch('checkLandscape');
      if (this.getIsPhone && this.getIsLandscape) {
        this.warning = false
      }
      else{
         this.warning = false
      }
    }
  }
}
</script>


<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-background-size: cover;
    background-size: cover;
    text-align: center;
    transition: background-color 0.4s ease-in-out;
    height: 100vh;
    bottom: 0;
    overflow-y: hidden;
    box-sizing: border-box;
    overflow-x: hidden;
  }

  .router-animate {
    animation-name: fadeOut, slideOutUp;
  }

  [v-cloak] {display: none}
  .router-transition {
    overflow-y: hidden;
  }

  .bm-burger-button {
    z-index:999;
  }
</style>
