<template>
    <div id="nav">
        <b-container fluid>
            <b-row class="pt-4 pb-3" align-h="end">
                <b-col cols="2" sm="2" md="2" lg="1" class="px-2 anjay">
                    <span @click="menuju('/')" :style="{color:getLinkColor}"
                        :class="[currentPage == '/' ? activeClass : '']" class="link"> Home </span>
                </b-col>
                <b-col cols="2" sm="2" md="2" lg="1" class="px-2 anjay">
                    <span @click="menuju('/about')" :style="{color:getLinkColor}"
                        :class="[currentPage == '/about' ? activeClass : '']" class="link"> About </span>
                </b-col>
                <b-col cols="2" sm="2" md="2" lg="1" class="px-2 anjay">
                    <span @click="menuju('/works')" :style="{color:getLinkColor}"
                        :class="[currentPage == '/works' ? activeClass : '']" class="link"> Works </span>
                </b-col>
                <b-col cols="2" sm="2" md="2" lg="1" class="px-2 anjay">
                    <span @click="menuju('/contact')" :style="{color:getLinkColor}"
                        :class="[currentPage == '/contact' ? activeClass : '']" class="link"> Contact </span>
                </b-col>
                <b-col cols="2" sm="2" md="2" lg="1" class="px-2 anjay">
                    <span @click="menuju('/inspect')" :style="{color:getLinkColor}"
                        :class="[currentPage == '/inspect' ? activeClass : '']" class="link"> Inspect </span>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';

    export default {
        name: 'Navbar',
        data() {
            return {
                activeClass: 'active',
            }
        },
        computed: {
            ...mapGetters(['getBackgroundColor', 'getLinkColor']),
            currentPage() {
                return this.$route.path;
            }
        },
        methods: {
            menuju(path) {
                if (this.$route.path !== path) this.$router.push(path);
                else this.$toast.error("You already here!", {timeout: 1500, position: "bottom-right"});
            }
        }
    }
</script>
<style>
    @import url('https://fonts.googleapis.com/css?family=Montserrat:400,800&display=swap');

    #nav {
        font-size: 1.2rem;
        font-family: 'Montserrat', sans-serif;
        background-color: transparent;
        font-weight:800;
    }

    .link {
        cursor: pointer;
        transition: color 0.6s ease-in-out;

    }
    .link:after {
        color:#FEC257;
        border-radius: 3em;
        border-top: .2em solid;
        content: "";
        position: absolute;
        right: 60%;
        bottom: -.3em;
        left: 40%;
        transition: right .4s cubic-bezier(0, .5, 0, 1);
    }

    .link:hover:after {
        right: 40%;
    }

    .active:after {
        color:#FEC257;
        border-radius: 3em;
        border-top: .2em solid;
        content: "";
        position: absolute;
        right: 40%;
        bottom: -.3em;
        left: 40%;
    }

    @media screen and (max-width: 575px) {
        #nav{
            font-size: 0.7rem;
        }
    }
</style>