const state = {
  backgroundColor: '#F4F6F9',
  linkColor: '#424953'
};

const getters = {
    getBackgroundColor: state => state.backgroundColor,
    getLinkColor: state => state.linkColor,
};

const actions = {
  async setBackgroundColor({ commit }, backgroundColor) {
    commit('setBackgroundColor', backgroundColor);
  },
  async setLinkColor({ commit }, linkColor) {
    commit('setLinkColor', linkColor);
  }
};

const mutations = {
  setBackgroundColor: (state, backgroundColor) => (state.backgroundColor = backgroundColor),
  setLinkColor: (state, linkColor) => (state.linkColor = linkColor),

};

export default {
  state,
  getters,
  actions,
  mutations
};
