import Vue from 'vue'
import VueRouter from 'vue-router'
import UnderConstruction from '../views/UnderConstruction.vue'

Vue.use(VueRouter)

const DEFAULT_TITLE = `Abudriman's online portfolio`
const IS_UNDER_CONSTRUCTION = false

const routes = [
  {
    path: '/',
    name: 'Home',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import ('../views/Home.vue'),
    meta: {
      title: `Home`
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else next()
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import ('../views/About.vue'),
    meta: {
      title: 'About'
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else if (from.name == 'Home' || from.name == 'Works' || from.name == 'Contact' || from.name == 'Inspect') next()
      else next('/')
    }
  },
  {
    path: '/works',
    name: 'Works',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import ('../views/Works.vue'),
    meta: {
      title: `Works`
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else next('/')
    }
  },
  {
    path: '/contact',
    name: 'Contact',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import ('../views/Contact.vue'),
    meta: {
      title: 'Contact'
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else next('/')
    }
  },
  {
    path: '/inspect',
    name: 'Inspect',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import ('../views/Inspect.vue'),
    meta: {
      title: 'Inspect'
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else next('/')
    }
  },
  {
    path: '/under_construction',
    name: 'UnderConstruction',
    component: UnderConstruction,
    meta: {
      title: 'Site Under Consruction'
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next()
      else next('/')
    }
  },
  {
    path: '/error',
    name: 'Error',
    component: () => import(/* webpackChunkName: "error" */ '../views/Error'),
    meta: {
      title: 'Page not found'
    },
    beforeEnter: (to, from, next) => {
      if(IS_UNDER_CONSTRUCTION) next('/under_construction')
      else next()
    }
  },
  {
    //this path should be placed in the very bottom end
    path: '*',
    beforeEnter: (ro, from, next) => {
      next('/error')
    }
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

router.afterEach((to) => {
  Vue.nextTick(() => {
    document.title = to.meta.title+' | '+DEFAULT_TITLE || DEFAULT_TITLE;
  })
})

export default router
