import Vue from 'vue'
import Vuex from 'vuex'
import globalStyling from './modules/globalStyling'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isPhone: false,
    isLandscape: false
  },
  mutations: {
    checkPhone: (state) => {
      if ("maxTouchPoints" in navigator) {
        state.isPhone = navigator.maxTouchPoints > 0;
      } else if ("msMaxTouchPoints" in navigator) {
        state.isPhone = navigator.msMaxTouchPoints > 0;
      } else {
        var mQ = window.matchMedia && matchMedia("(pointer:coarse)");
        if (mQ && mQ.media === "(pointer:coarse)") {
          state.isPhone = !!mQ.matches;
        } else if ('orientation' in window) {
          state.isPhone = true; // deprecated, but good fallback
        } else {
          // Only as a last resort, fall back to user agent sniffing
          var UA = navigator.userAgent;
          state.isPhone = (
            /\b(BlackBerry|webOS|iPhone|IEMobile)\b/i.test(UA) ||
            /\b(Android|Windows Phone|iPod)\b/i.test(UA)
          );
        }
      }
    },

    checkLandscape: (state) => {
       state.isLandscape = window.screen.orientation.type.includes("andscape")
    },
  },
  getters: {
    getIsPhone: state => state.isPhone,
    getIsLandscape: state => state.isLandscape,
  },
  actions: {
    async checkPhone({ commit }) {
      commit('checkPhone');
    },
    async checkLandscape({ commit }) {
      commit('checkLandscape');
    }
  },
  modules: {
    globalStyling
  }
})
